@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-attachment:fixed;background-image:linear-gradient(rgba(34,33,33,0.75), rgba(34,33,33,0.75)),url(/lowPolyRobotsAttack/static/media/retrowave-background.ac5c1261.jpg);background-size:cover;font-family:'Press Start 2P', cursive}.app{display:grid;grid-template-rows:auto auto auto;grid-template-columns:repeat(auto-fit, 1fr);grid-template-areas:"about" "title" "level-selection";justify-content:stretch;justify-items:stretch;grid-gap:1em}.about{grid-area:about;justify-self:end;color:white;padding:1rem}a{color:white;text-decoration:underline}a>h5:hover{color:#FF3CC3;text-decoration:underline}.about h5{margin-right:2rem;text-decoration:underline}.about a{color:white}.title{grid-area:title;height:100%;width:100%;text-align:center}.title div{margin-top:16vh;margin-left:8vw;margin-right:8vw;margin-bottom:4vh}.title h1{font-size:3rem}.glow{font-size:3rem;color:#FF83CF;text-align:center;-webkit-animation:glow 1s ease-in-out infinite alternate;animation:glow 1s ease-in-out infinite alternate}@-webkit-keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}@keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}.title h4{color:#FF83CF}.title h5{color:#D11087;font-size:1rem}.title h6{color:white}.lvl-selection{grid-area:level-selection;background-color:rgba(0,0,0,0.6);width:100%}.lvl-selection .card-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));align-items:center;grid-gap:2.5rem;padding:3.5rem}.card{justify-self:center;padding:1rem;border:2px solid #00B9F0;height:95%;width:80%;color:white;text-align:center;transition-timing-function:ease-out;transition:0.2s}.card:hover{transition-timing-function:ease-in;transition:0.2s;transform:translateY(-5%);-webkit-animation:glow-card 1s ease-in-out infinite alternate;animation:glow-card 1s ease-in-out infinite alternate}@-webkit-keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}@keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}.card h4{color:#00B9F0}.card p{text-align:center;font-size:0.95rem}.card button{margin-top:3vh;margin-bottom:4vh;width:100%;height:20%;font-family:'Press Start 2P', cursive;background-color:#00B9F0;border:none}.play{border:2px solid #FF83CF}.play:hover{-webkit-animation:glow-card-pink 1s ease-in-out infinite alternate;animation:glow-card-pink 1s ease-in-out infinite alternate}@-webkit-keyframes glow-card-pink{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #FF83CF,0 6px 8px #FF83CF,0 8px 10px #FF83CF,0 12px 40px #FF83CF}}@keyframes glow-card-pink{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #FF83CF,0 6px 8px #FF83CF,0 8px 10px #FF83CF,0 12px 40px #FF83CF}}.play h1{color:#FF3CC3}.card.play h4{color:rgba(0,0,0,0)}.play button{background-color:#FF3CC3}

.authors-section{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}.information-col{justify-self:stretch;align-self:stretch;background-color:#40383C}.information-col h1{margin:4rem;font-size:4rem;color:#FF83CF;text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}.author-desc{align-self:stretch;display:grid;grid-template-rows:20% auto 35%;grid-template-columns:15rem 1fr;grid-template-areas:"img author" "img task" "img link";margin-top:4rem;margin-bottom:4rem;color:white}.img-container{grid-area:img;justify-content:center;align-self:center}.img-container img{height:15rem;width:15rem}.author-name{grid-area:author}.author-name.Sergio{text-shadow:0 3px 3px #FF3CC3,0 3px 3px #FF3CC3,0 6px 10px #FF3CC3,0 8px 40px #FF3CC3}.author-name.Andrea{text-shadow:0 3px 3px #00FF27,0 3px 3px #00FF27,0 6px 10px #00FF27,0 8px 40px #00FF27}.author-task{grid-area:task;font-family:'VT323', monospace;width:90%}.author-task p{font-size:2rem}.author-links{grid-area:link;align-self:center;font-family:'VT323', monospace}.author-links h5{font-size:1.5rem;display:inline;position:relative;bottom:1rem}.author-links span{margin:0.5rem}.author-links span svg{height:3rem}.author-links span svg:last-child{margin-left:1rem}.Sergio-label{color:#FF3CC3}.Sergio-label a{color:#FF3CC3}.Andrea-label{color:#00FF27}.Andrea-label a{color:#00FF27}.Andrea svg path{fill:#00FF27}.ghost-col{justify-self:stretch;align-self:stretch;background-image:linear-gradient(rgba(99,91,91,0.75), rgba(64,56,60,0.75)),url(/lowPolyRobotsAttack/static/media/ghost-background.30b04763.png);color:white;padding:2rem}.ghost-col .ghost-desc,.ghost-col h4{text-align:center;text-shadow:0 3px 3px #FF3CC3,0 3px 3px #FF3CC3,0 6px 8px #FF3CC3,0 6px 10px #FF3CC3}.ghost-col div{text-align:center}.ghost-col svg{width:40%;height:40%;margin:1rem;-webkit-animation:glow-ghost 1s ease-in-out infinite alternate;animation:glow-ghost 1s ease-in-out infinite alternate}@-webkit-keyframes glow-ghost{from{-webkit-filter:drop-shadow(0 6px w0px #FF3CC3);filter:drop-shadow(0 6px w0px #FF3CC3)}to{-webkit-filter:drop-shadow(0 6px 20px #D11087);filter:drop-shadow(0 6px 20px #D11087)}}@keyframes glow-ghost{from{-webkit-filter:drop-shadow(0 6px w0px #FF3CC3);filter:drop-shadow(0 6px w0px #FF3CC3)}to{-webkit-filter:drop-shadow(0 6px 20px #D11087);filter:drop-shadow(0 6px 20px #D11087)}}.ghost-col h5{width:90%;margin:2rem}.ghost-col h4{margin-top:8rem;margin-bottom:2rem}#pixeled_git path:hover{fill:#b8b3b3;stroke:white;stroke-width:2px}#pixeled_lnkdn path:hover{fill:#07f;stroke:white;stroke-width:2px}

